.image-grid1 {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 10px;
  }
  
  .image-item {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .image-item img {
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
  }


  